@import "normalize.css";
* {
  margin: 0;
  padding: 0;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page {
  height: 100%;
}
.page-body {
  font-family: "Montserrat", "Arial", "sans-serif";
  font-weight: 500;
  color: #1a2f3f;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  min-height: 100%;
  display: grid;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  width: 100%;
  max-width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: max-content;
}
.header__users-block-container,
.header__info-block-container,
.header__nav-block-container {
  margin: 0 auto;
  max-width: 1350px;
}
.header__users-block-wrap {
  display: flex;
  justify-content: space-between;
  padding: 10px 30px 0;
}
.header__info-block-wrap {
  display: flex;
  justify-content: space-between;
  padding: 0 30px;
  padding-top: 17px;
}
.header__burger-wrap {
  display: none;
}
.header__users-block {
  width: 100%;
  max-width: 100%;
  background-color: rgba(245, 245, 245, 0.5);
  min-height: 38px;
}
.header__info-social-block {
  display: flex;
  justify-content: space-between;
  flex: 0 0 79px;
}
.header__social-link {
  display: block;
  background: url("../../img/facebook.svg") no-repeat center;
  width: 13px;
  height: 13px;
}
.header__social-link:nth-child(2) {
  background: url("../img/instagram.svg") no-repeat center;
  width: 13px;
  height: 13px;
  padding-top: 14px;
}
.header__social-link:nth-child(3) {
  background: url("../../img/youtube.svg") no-repeat center;
  width: 16px;
  height: 16px;
}
.header__main-par {
  font-weight: 600;
  font-size: 14px;
  line-height: 17.07px;
  color: #337d5a;
  flex: 0 0 max-content;
  padding-left: 14.4%;
  padding-top: 1px;
}
.header__users-tel {
  font-weight: 600;
  font-size: 12px;
  line-height: 14.63px;
  color: #000000;
  display: none;
}
.header__contacts-tel-elem {
  color: #337d5a;
}
.header__net-users-link-wrap {
  display: none;
  border-bottom: 2px solid #337d5a;
}
.header__net-users-link {
  font-weight: 600;
  font-size: 13px;
  line-height: 15.26px;
  letter-spacing: 0.02em;
  color: #337d5a;
}
.header__users-nav {
  display: flex;
  padding-top: 2px;
}
.header__users-link {
  display: block;
  font-family: "Raleway", "Arial", "sans-serif";
  font-size: 13px;
  line-height: 15.26px;
  letter-spacing: 0.02em;
  color: #323232;
}
.header__users-link-in {
  padding-left: 20px;
  margin-right: 26px;
  position: relative;
}
.header__users-link:nth-child(3) {
  position: relative;
  width: max-content;
  padding-right: 13px;
}
.header__users-link-in::before {
  content: "";
  position: absolute;
  background: url("../../img/user.svg") no-repeat center;
  width: 11px;
  height: 11px;
  left: 2px;
  top: 2px;
}
.header__users-link:nth-child(3)::before {
  content: "";
  position: absolute;
  background: url("../../img/open-speak.svg") no-repeat center;
  width: 8px;
  height: 6px;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto 0;
}
.header__users-link-wrapper {
  margin-right: 49px;
  border-bottom: 2px solid #93b474;
}
.header__users-link-color {
  color: #000000;
}
.header__info-block {
  width: 100%;
  max-width: 100%;
  min-height: 86px;
}
.header__logo {
  display: flex;
}
.header__logo-link {
  display: block;
  margin-right: 8px;
}
.header__logo-link-sign {
  font-family: "Raleway", "Arial", "sans-serif";
  font-weight: 800;
  font-size: 15px;
  line-height: 17.61px;
  letter-spacing: 0.02em;
  color: #1a2f3f;
  padding-top: 11px;
}
.header__block-contacts {
  display: flex;
  margin-left: auto;
  margin-right: 7.2%;
  padding-top: 6px;
}
.header__list-soc {
  display: flex;
  margin-right: 31px;
}
.header__item-soc-contacts-link {
  display: block;
  width: 31px;
  height: 31px;
  border-radius: 80px;
}
.header__item-soc-contacts:nth-child(1) {
  margin-right: 10px;
}
.header__item-soc-contacts:nth-child(1) .header__item-soc-contacts-link {
  background: #337d5a url("../../img/viber.svg") no-repeat center;
  background-size: 17px 17px;
  display: block;
  width: 31px;
  height: 31px;
}
.header__item-soc-contacts:nth-child(2) {
  margin-right: 10px;
}
.header__item-soc-contacts:nth-child(2) .header__item-soc-contacts-link {
  background: #337d5a url("../../img/telegram.svg") no-repeat center;
  background-size: 14px 14px;
}
.header__item-soc-contacts:nth-child(3) .header__item-soc-contacts-link {
  background: #337d5a url("../../img/whatsapp.svg") no-repeat center;
  background-size: 15px 15px;
}
.header__block-tel {
  display: flex;
  flex-direction: column;
  padding-top: 1px;
}
.header__contacts-tel {
  color: #000000;
  font-weight: 600;
  font-size: 13px;
  line-height: 15.85px;
}
.header__contacts-tel:first-child {
  margin-bottom: 2px;
}
.header__block-net {
  display: flex;
  padding-right: 5%;
  padding-top: 4px;
}
.header__net-link {
  font-family: "Raleway", "Arial", "sans-serif";
  font-weight: 600;
  font-size: 13px;
  line-height: 15.26px;
  letter-spacing: 0.02em;
  background-color: #337d5a;
  width: 148px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  margin-right: 15px;
}
.header__text {
  font-size: 12px;
  line-height: 14.63px;
  letter-spacing: 0.02em;
  color: #8a8a8a;
  padding-top: 7px;
}
.header__block-bag {
  padding-top: 11px;
  padding-right: 5px;
  position: relative;
}
.header__bag-link {
  display: block;
  background: url("../../img/bag.svg") no-repeat center;
  width: 24px;
  height: 24px;
}
.header__block-bag-count {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15px;
  height: 15px;
  background-color: #93b474;
  color: #ffffff;
  font-weight: 700;
  font-size: 8px;
  line-height: 9.75px;
  border-radius: 50%;
  position: absolute;
  right: 0;
  top: 11px;
}
.header__nav-block {
  width: 100%;
  max-width: 100%;
  min-height: 55px;
  transition: all 0.5s ease 0s;
}
.header__nav-block-wrap {
  padding: 0 44px 0 45px;
  padding-top: 21px;
}
.header__list {
  display: flex;
  justify-content: space-evenly;
}
.header__item {
  flex: 0 0 max-content;
  padding: 0 8px;
  padding-bottom: 11px;
}
.header__item:nth-child(3) {
  padding-left: 5px;
}
.header__item:nth-child(5) {
  padding-right: 6px;
}
.header__item--current {
  border-bottom: 3px solid #337d5a;
}
.header__item--current .header__item-link {
  color: #337d5a;
}
.header__item-link {
  font-family: "Raleway", "Arial", "sans-serif";
  font-size: 15px;
  line-height: 17.61px;
  letter-spacing: calc(15px * 0.02);
  color: #1a2f3f;
}
@media (max-width: 1023px) {
  .header.active .header__nav-block {
    min-height: 293px;
  }
  .header.active .header__nav {
    transform: translate(0%, 0%);
  }
  .header.active .header__burger::before {
    transform: translate(0, 4px) rotate(45deg);
  }
  .header.active .header__burger::after {
    transform: translate(0, -3px) rotate(-45deg);
  }
  .header.active .header__burger-element {
    transform: scale(0);
    transition: all 0.5s ease 0s;
  }
  .header__burger::before,
  .header__burger::after {
    content: "";
    width: 100%;
    height: 2.6px;
    background-color: #000000;
    position: absolute;
    transition: all 0.5s ease 0s;
  }
  .header__burger::before {
    top: 0;
  }
  .header__burger::after {
    top: 0;
    bottom: 0;
    margin: auto 0;
  }
  .header__burger-element {
    width: 70%;
    height: 3px;
    position: absolute;
    bottom: 0;
    background-color: #000000;
    z-index: 5;
  }
  .header__burger-wrap {
    display: block;
    z-index: 10;
  }
  .header__burger {
    width: 19px;
    height: 17px;
    position: relative;
  }
  .header__logo {
    flex-direction: column;
    align-items: center;
    width: 170px;
  }
  .header__logo-link {
    margin-right: 0;
  }
  .header__logo-link-sign {
    display: block;
    text-align: center;
    padding-top: 5px;
  }
  .header__brake {
    display: initial;
  }
  .header__block-contacts,
  .header__block-net {
    display: none;
  }
  .header__nav-block-wrap {
    padding: 0;
  }
  .header__nav {
    transform: translate(0%, -100vh);
    position: absolute;
    transition: all 0.5s ease 0s;
    width: 100vw;
    left: 0;
  }
  .header__list {
    flex-direction: column;
  }
  .header__item {
    padding: 0;
    position: relative;
  }
  .header__item::before {
    content: "";
    position: absolute;
    width: 97%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.04);
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  .header__item--current {
    border: none;
  }
  .header__item--current .header__item-link {
    color: #ffffff;
    background-color: #337d5a;
  }
  .header__item-link {
    width: 100%;
    height: 100%;
    display: block;
    padding: 15px 0;
    padding-left: 20px;
  }
  .header__info-block-wrap {
    align-items: center;
  }
}
@media (max-width: 767px) {
  .header.active .header__block-bag {
    display: none;
  }
  .header.active .header__burger-wrap {
    order: 1;
  }
  .header.active .header__users-tel,
  .header.active .header__net-users-link-wrap,
  .header.active .header__users-link-mode {
    display: none;
  }
  .header.active .header__users-block {
    order: 1;
  }
  .header.active .header__nav-block {
    order: 2;
  }
  .header.active .header__users-link-in,
  .header.active .header__users-link-wrapper {
    display: block;
  }
  .header.active .header__info-social-block {
    display: flex;
    order: 1;
  }
  .header.active .header__logo {
    flex-direction: row;
    padding-left: 0;
    justify-content: left;
  }
  .header.active .header__logo-link-sign {
    text-align: left;
  }
  .header.active .header__logo-link {
    margin-right: 10px;
    padding-top: 3px;
  }
  .header.active .header__info-block-wrap {
    padding: 17px 15px 0;
  }
  .header.active .header__users-block-wrap {
    padding: 10px 10px 0;
  }
  .header.active .header__social-link:nth-child(1),
  .header.active .header__social-link:nth-child(2) {
    padding-top: 13px;
  }
  .header__users-block-wrap {
    padding: 0 0 0 12px;
    align-items: center;
  }
  .header__users-nav {
    padding: 0;
  }
  .header__users-link:nth-child(3) {
    border: 1px solid #93b474;
    height: 38px;
    padding: 5px;
    padding-right: 17px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header__users-link:nth-child(3)::before {
    background: url("../../img/open-mob.svg") no-repeat center;
    width: 10px;
    height: 8px;
    right: 4px;
    top: 0;
    bottom: 0;
  }
  .header__users-tel {
    position: relative;
    padding-left: 15px;
  }
  .header__users-tel::before {
    content: "";
    position: absolute;
    background: url("../../img/tel.svg") no-repeat center;
    width: 10px;
    height: 10px;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto 0;
  }
  .header__logo {
    padding-left: 10px;
    width: 200px;
  }
  .header__logo-img {
    width: 29px;
    height: 28px;
  }
  .header__main-par,
  .header__info-social-block,
  .header__users-link-in,
  .header__users-link-wrapper {
    display: none;
  }
  .header__users-tel,
  .header__net-users-link-wrap {
    display: block;
  }
  .header__burger-element {
    height: 2px;
  }
}
.showing {
  width: 100%;
  max-width: 100%;
}
.showing__wrap {
  position: relative;
  min-height: 600px;
}
.showing__video {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  top: 0;
  right: 0;
  left: 0;
  object-fit: cover;
  z-index: 1;
}
.showing.active .showing__block {
  opacity: 0;
  transition: all 0.5s ease 0s;
}
.showing.active .showing__link {
  opacity: 0;
}
.showing__content {
  background: rgba(0, 0, 0, 0.45);
  position: relative;
  width: 100%;
  height: 600px;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 56px;
}
.showing__title {
  font-family: "Raleway", "Arial", "sans-serif";
  font-weight: 800;
  font-size: 62px;
  line-height: 72.79px;
  letter-spacing: 0.01em;
  color: #ffffff;
  text-align: center;
  margin-bottom: 31px;
}
.showing__text {
  font-family: "Raleway", "Arial", "sans-serif";
  font-size: 25px;
  line-height: 32px;
  text-align: center;
  letter-spacing: 0.01em;
  color: #ffffff;
  width: 35%;
  margin-bottom: 38px;
  font-weight: 600;
}
.showing__link {
  display: flex;
  border: 2px solid #ffffff;
  width: 61px;
  height: 61px;
  border-radius: 50%;
  position: relative;
  transition: all 1s ease 0s;
}
.showing__link::after {
  content: "";
  position: absolute;
  background: url("../../img/play.svg") no-repeat center;
  width: 12px;
  height: 17px;
  left: 5px;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
.showing__block {
  position: absolute;
  width: 100%;
  min-height: 600px;
  top: 0;
  z-index: 7;
  background: url("../../img/video.jpg") no-repeat center;
  background-size: cover;
}
@media (max-width: 1023px) {
  .showing__text {
    width: 55%;
    font-size: 20px;
  }
  .showing__title {
    font-weight: 600;
    font-size: 50px;
  }
}
@media (max-width: 767px) {
  .showing__text {
    width: 80%;
  }
  .showing__title {
    font-weight: 600;
    font-size: 40px;
  }
}
.footer {
  width: 100%;
  max-width: 100%;
}
.footer__container {
  margin: 0 auto;
}
.product {
  width: 100%;
  max-width: 100%;
}
.product__container {
  margin: 0 auto;
  max-width: 1350px;
}
.product__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 83px 23px 10px;
}
.product__title {
  font-weight: 800;
  font-size: 45px;
  line-height: 53px;
  letter-spacing: 0.01em;
  font-family: "Raleway", "Arial", "sans-serif";
  margin-bottom: 30px;
}
.product__text {
  font-size: 18px;
  line-height: 28px;
  text-align: center;
  letter-spacing: 0.01em;
  font-family: "Raleway", "Arial", "sans-serif";
  width: 47%;
  margin-bottom: 61px;
}
.product__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 1em;
  row-gap: 3.1em;
  justify-content: center;
  width: 100%;
}
.product__item {
  border: 1px solid rgba(0, 0, 0, 0.08);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 410px;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 14px;
}
.product__item:nth-child(2),
.product__item:nth-child(3) {
  padding-top: 22px;
}
.product__item:nth-child(4),
.product__item:nth-child(5),
.product__item:nth-child(6) {
  padding-top: 50px;
}
.product__item--current {
  border: 1.5px solid #93b474;
}
.product__block-image {
  padding-bottom: 20px;
  position: relative;
}
.product__discount-wrap {
  position: absolute;
  top: -1px;
  left: -3px;
}
.product__discount {
  position: relative;
  width: 85px;
}
.product__svg {
  position: absolute;
  left: 0;
  top: 0;
}
.product__loop-wrap-main {
  width: 60px;
  height: 60px;
  position: absolute;
  left: 0;
  right: 2px;
  margin: auto;
  transform: translate(0, -174%);
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product__loop-wrap {
  position: relative;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  background: rgba(255, 255, 255, 0.15);
}
.product__loop-wrap::after {
  content: "";
  position: absolute;
  background: url("../../img/loop.svg") no-repeat center;
  width: 19px;
  height: 19px;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
}
.product__loop {
  position: absolute;
  background-color: #93b474;
  border-radius: 50%;
  border: none;
  width: 45px;
  height: 45px;
  left: 5px;
  top: 0;
  bottom: 0;
  margin: auto;
}
.product__btn {
  position: absolute;
  width: 30px;
  height: 30px;
  background: url("../../img/left-arrow.svg") no-repeat center;
  background-size: 23px 23px;
  border: none;
  transform: translate(0, 386%);
  left: 10px;
}
.product__btn:nth-of-type(2) {
  background: url("../../img/right-arrow.svg") no-repeat center;
  background-size: 23px 23px;
  right: 12px;
  left: auto;
}
.product__img-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 2px;
}
.product__img-wrap img {
  object-fit: cover;
}
.product__item:nth-child(2) .product__img-wrap {
  padding-left: 3px;
}
.product__item:nth-child(3) .product__img-wrap {
  padding-left: 5px;
}
.product__item:nth-child(4) .product__img-wrap {
  padding-left: 4px;
}
.product__item:nth-child(5) .product__img-wrap,
.product__item:nth-child(6) .product__img-wrap {
  padding-right: 4px;
}
.product__block-content {
  display: flex;
  flex-direction: column;
  padding-top: 18px;
}
.product__item:nth-child(4) .product__block-content,
.product__item:nth-child(5) .product__block-content,
.product__item:nth-child(6) .product__block-content {
  padding-top: 34px;
}
.product__discount-act {
  font-weight: 800;
  font-size: 10px;
  line-height: 11.74px;
  letter-spacing: 0.06em;
  font-family: "Raleway", "Arial", "sans-serif";
  color: #ffffff;
  position: absolute;
  left: 20px;
  top: 10px;
  z-index: 5;
}
.product__item:nth-child(2) .product__discount-act {
  left: 13px;
}
.product__title-content {
  font-family: "Raleway", "Arial", "sans-serif";
  font-weight: 700;
  font-size: 18px;
  line-height: 21.13px;
  letter-spacing: 0.01em;
  padding-left: 5%;
  margin-bottom: 7px;
}
.product__item:nth-child(4) .product__title-content {
  margin-bottom: 6px;
}
.product__code {
  font-size: 15px;
  line-height: 18.29px;
  letter-spacing: 0.01em;
  padding-left: 5%;
  color: #8a8a8a;
  margin-bottom: 11px;
}
.product__item:nth-child(4) .product__code,
.product__item:nth-child(5) .product__code,
.product__item:nth-child(6) .product__code {
  margin-bottom: 10px;
}
.product__desc {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  font-family: "Raleway", "Arial", "sans-serif";
  padding-left: 5.3%;
  letter-spacing: 0.01em;
  width: 80%;
  color: #000000;
}
.product__info-block {
  display: flex;
  justify-content: space-between;
  padding: 15px 7.5% 17px 6.8%;
}
.product__item:nth-child(2) .product__info-block,
.product__item:nth-child(3) .product__info-block {
  padding: 15px 7.5% 16px 6.8%;
}
.product__info-block-wrap {
  position: relative;
}
.product__info-block-wrap::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.08);
}
.product__info {
  display: flex;
  padding-left: 20px;
  position: relative;
}
.product__info:first-child {
  padding-top: 1px;
}
.product__item:nth-child(2) .product__info:first-child,
.product__item:nth-child(3) .product__info:first-child,
.product__item:nth-child(5) .product__info:first-child,
.product__item:nth-child(6) .product__info:first-child {
  padding-left: 27px;
}
.product__info:first-child:after {
  content: "";
  background: url("../../img/weight.svg") no-repeat center;
  position: absolute;
  width: 18px;
  height: 18px;
  left: -7px;
  top: 7px;
}
.product__info:last-child::after {
  position: absolute;
  content: "";
  background: url("../../img/package.svg") no-repeat center;
  width: 12px;
  height: 18px;
  left: -6px;
  top: 6px;
}
.product__info-main {
  font-size: 14px;
  line-height: 17.07px;
  letter-spacing: 0.01em;
  color: #8a8a8a;
}
.product__info-sec {
  color: #337d5a;
}
.product__block-cost {
  display: flex;
  justify-content: space-between;
  padding: 14px 5% 0;
}
.product__cost {
  display: flex;
  align-items: baseline;
  column-gap: 0.5em;
}
.product__price {
  font-weight: 600;
  font-size: 15px;
  line-height: 18.29px;
  letter-spacing: 0.01em;
}
.product__rate-main {
  font-weight: 600;
  font-size: 20px;
  line-height: 24.38px;
  letter-spacing: 0.01em;
  color: #337d5a;
  padding-top: 7px;
  transform: translate(0, 4%);
}
.product__rate-main-mode {
  margin-right: 3px;
}
.product__rate {
  font-size: 15px;
  line-height: 18.29px;
}
.product__value-main {
  font-size: 16px;
  line-height: 19.5px;
  letter-spacing: 0.01em;
  color: #8a8a8a;
  text-decoration: line-through;
  transform: translate(0, 9%);
}
.product__value {
  font-size: 13px;
  line-height: 15.85px;
}
.product__item:nth-child(4) .product__wrap-btn-content,
.product__item:nth-child(5) .product__wrap-btn-content,
.product__item:nth-child(6) .product__wrap-btn-content {
  padding-right: 2px;
}
.product__btn-content {
  font-family: "Raleway", "Arial", "sans-serif";
  font-weight: 700;
  font-size: 14px;
  line-height: 16.44px;
  letter-spacing: 0.02em;
  color: #ffffff;
  background-color: #337d5a;
  width: 125px;
  height: 41px;
  border: none;
}
@media (max-width: 767px) {
  .product__text {
    width: 71%;
  }
}
